import React, {FunctionComponent} from 'react';

interface Props {
  value?: number,
  onRoll?: () => void,
}

const Dice: FunctionComponent<Props> = ({
  value,
  onRoll,
}) => {
  const text = value ? String.fromCodePoint(value + 0x267f) : 'roll';
  const onClick = (e: any) => {
    if (!value && onRoll) {
      onRoll();
    }
  };
  return (
    <div className="Dice" onClick={onClick}>{text}</div>
  );
};

export default Dice;
